<template>
  <div>
    <button
      class="btn btn-secondary mb-0 mr-2"
      type="button"
      @click="exportGeneratedItems"
      :disabled="!hasGeneratedItems"
    >
      <i class="fa fa-cloud-download mr-2"></i> Exportar
    </button>
    <label class="btn btn-secondary mb-0" for="importItems">
      <i class="fa fa-cloud-upload"></i> Importar
    </label>
    <input
      class="d-none m-0"
      type="file"
      id="importItems"
      @change="importItems($event.target)"
      :multiple="false"
      lang="pt-br"
      accept=".json"
      hidden
    />
  </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {
    name: 'ImportItems',
    computed: {
      ...mapGetters([ 'hasGeneratedItems' ])
    },
    methods: {
      ...mapActions([
        'exportGeneratedItems',
        'importGeneratedItems'
      ]),
      importItems ({ files }) {
        this.importGeneratedItems(files[0]);
      }
    }
  }
</script>