<template>
  <div class="item-preview">
    <div class="generate-options">
      <div class="row align-items-md-center">
        <div class="order-0 order-md-0 col-12 col-md-4 mb-2 mb-md-0">
          <div class="form-inline">
            <label class="my-1 mx-md-1" for="itemName">
              <strong>Nome do artefato:</strong>
            </label>
            <input
              class="form-control mx-md-1 my-1"
              type="text"
              id="itemName"
              v-model="itemName"
            />
          </div>
        </div>
        <div class="order-2 order-md-1 col-12 col-md-4 mb-2 mb-md-0 d-md-flex justify-content-md-center">
          <button
            class="btn btn-dark text-left text-light"
            type="button"
            @click="generateImage"
          >
            <i class="ra ra-fairy-wand mr-2"></i>
            <strong v-if="item.uuid">Reconstruir item mágico</strong>
            <strong v-else>Criar item mágico</strong>
          </button>
        </div>
        <div class="order-2 col-12 col-md-4 mb-2 mb-md-0 d-none d-md-flex justify-content-md-end">
          <ItemPreviewSize v-model="scale" />
        </div>
      </div>
    </div>
    <div class="d-flex justify-content-center align-items-center h-100">
      <div
        class="item-cards"
        :class="[previewSize]"
        ref="itemCards"
      >
        <ItemCard class="gold-card">
          <BackgroundCard :item="item" />
        </ItemCard>
        <ItemCard class="gold-card">
          <FrontCard :item="item" />
        </ItemCard>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState, mapActions } from 'vuex';
  import { mapFields } from 'vuex-map-fields';

  import BackgroundCard from './BackgroundCard.vue';
  import FrontCard from './FrontCard.vue';
  import ItemCard from './ItemCard.vue';
  import ItemPreviewSize from './ItemPreviewSize.vue';

  export default {
    name: 'ItemPreview',
    components: {
      BackgroundCard,
      FrontCard,
      ItemCard,
      ItemPreviewSize,
    },
    data () {
      return {
        scale: 'md'
      }
    },
    computed: {
      ...mapState([ 'item' ]),
      ...mapFields({
        itemName: 'item.name'
      }),
      previewSize () {
        return  `item-preview-${this.scale}`;
      }
    },
    methods: {
      ...mapActions([ 'setGeneratedItem' ]),
      async exportItem () {
        const el = this.$refs.itemCards;

        return await this.$html2canvas(el);
      },
      async generateImage () {
        const canvas = await this.exportItem();

        this.setGeneratedItem({
          name: `${this.item.name}.jpg`,
          url: canvas.toDataURL('image/jpeg'),
          width: canvas.width,
          height: canvas.height
        });
      },
      toggleSize (value) {
        this.scale = value;
      }
    }
  }
</script>

<style lang="scss" scoped>
  .item-preview {
    height: 100%;
    max-height: 100%;
    max-width: 100%;
    width: 100%;

    @media (max-width: 576px) {
      height: auto;
      max-height: auto;
    }

    $transition: transform 1s ease-in-out;

    $maxWidth: 1500px;

    .item-cards {
      display: flex;
      margin: 4rem auto 0 auto;
      max-width: $maxWidth;
      overflow: hidden;
      position: relative;
      width: $maxWidth;

      @media (max-width: 576px) {
        zoom: 0.25 !important;
      }
    }

    .generate-options {
      background-color: rgba(white, .95);
      height: auto;
      left: 0;
      padding: .125rem 1rem;
      position: absolute;
      top: 0;
      width: 100%;

      @media (max-width: 576px) {
        background-color: transparent;
        position: relative;
      }
    }

    .item-preview-sm {
      zoom: 0.33;
    }

    .item-preview-md {
      zoom: 0.45;
    }

    .item-preview-lg {
      zoom: 0.6;
    }
  }
</style>