import { MERGE_IMPORTED_ITEMS, RESET_ITEM_DATA, SET_GENERATED_ITEM, SET_ITEM_DATA, SET_ITEM_FILE } from './mutations';
import { downloadObjectAsJson, getFileStructure, readJsonFile } from './utils';

export default {
  editGeneratedItem ({ commit, state: { generatedItems } }, uuid) {
    commit(SET_ITEM_DATA, generatedItems[uuid]);
  },
  async importGeneratedItems ({ commit }, file) {
    const importedItems = await readJsonFile(file);

    commit(MERGE_IMPORTED_ITEMS, importedItems);
  },
  exportGeneratedItems ({ state: { generatedItems } }) {
    downloadObjectAsJson(generatedItems, 'artefatos');
  },
  setGeneratedItem ({ commit }, image) {
    commit(SET_GENERATED_ITEM, image);
    commit(RESET_ITEM_DATA);
  },
  async setItemFile ({ commit }, rawFile) {
    const file = await getFileStructure(rawFile);

    commit(SET_ITEM_FILE, file);
  }
}